@import url(https://fonts.googleapis.com/css2?family=Mukta&display=swap);
.cover-page {
  display: flex;
  height: 100vh;
  background-image: linear-gradient(to right, purple, blue)
}

.cover-left {
  width: 50%;
  border-radius: 50%;
  border-top-right-radius: 0;
  border-bottom-right-radius: 290px;
  border-bottom-left-radius: 0;
  border-top-left-radius: 290px;
  width: 700px;
  height: 700px;
  margin: 50px;
  border: 4px solid green;
}

.cover-right h1 {
  font-size: 3.4rem;
}

.cover-right p {
  font-weight: 600;
}

.cover-right .connect-msg {
  font-style: italic;
}

.cover-right button {
  padding: 10px 20px;
  border-radius: 15px;
  border: 5px solid rgba(0, 128, 0, 0.74);
  margin: 20px 0;
  font-weight: 600;
}

.token_card {
  width: 360px;
  height: 450px;
  box-shadow: 1px 3px 5px gray;
  border-radius: 10px;
  padding: 20px;
  margin: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.lbl {
  font-size: 25px;
}

.content {
  text-align: center;
  font-family: monospace;
  margin-bottom: 20px;
}

.btns {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  margin-top: 20px;
  flex-wrap: wrap;
}

.btns button {
  font-size: 17px;
  border: 1px solid gray;
  padding: 5px 12px;
  box-shadow: 1px 1px 5px gray;
  border-radius: 5px;
}

.seller-addr {
  font-family: monospace;
}

* {
  font-family: "Mukta", sans-serif;
}

.add-btn {
  margin-top: 30px;
  border-radius: 10px;
  border-color: blue;
  padding: 5px 10px;
}

.all_nft {
  display: flex;
  flex-wrap: wrap;
}
.nav {
    height: 160px;    
}

.logo_img {
    height: 120px;
    width: 120px;
    border-radius: 40px;
}

.nav-item {
    display: flex;
    justify-content: center;
    align-items: center;    
}


.nav-item button {
    border-radius: 5px;
    padding: 5px 10px;
    border-color: blue;
    background-color: white;
}
